import React, { useState, useEffect } from 'react';
import ArticlesList from '../components/ArticlesList';
import CommentsList from '../components/CommentsList';
import AddComment from '../components/AddComment';
import UpvoteSection from '../components/UpvoteSection';
import articleContent from './article-content';
import NotFoundPage from './NotFoundPage';

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => {
        return article.name === name;
    })

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

    // useEffect(()=>{
    //     const fetchData = ()=>{
    //         fetch(`/apis/article/${name}`)
    //         .then(resp => resp.json())
    //         .then(res=> console.log(res));
    //         //setArticleInfo(body)
    //     }
    //     fetchData();

    // },[name])
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            console.log(body);
            setArticleInfo(body);
        }
        fetchData();
    }, [name]);


    if (!article) {
        return <NotFoundPage />
    }
    const otherArtciles = articleContent.filter(article => (
        article.name !== name
    ))
    return (
        <>
            <h1>{name} </h1>
            <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            {article.content.map((para, key) => {
                return <p key={key}> {para}</p>
            })}
            <CommentsList comments={articleInfo.comments} />
            <AddComment articleName={name} comments={articleInfo.comments} setArticleInfo={setArticleInfo} />Î
            <h3>Other Articles:</h3>
            <ArticlesList articles={otherArtciles} />
        </>
    );
}

export default ArticlePage;