import React from 'react';

const UpvoteSection = ({ articleName, upvotes, setArticleInfo }) => {
    // const [upvotes, setUpvotes] = useState(upvotes);
    const upvoteArticle = async () => {
        // await fetch(`api/articles/${articleName}/upvote`,{
        //     method: 'post'
        // })
        // .then(resp=>resp.json())
        // .then(res=>{
        //     console.log(res);
        //     setArticleInfo(res)
        // })
        const result = await fetch(`/api/articles/${articleName}/upvote`, { method: 'post' });
        const body = await result.json();
        console.log(body);
        setArticleInfo(body);
    }
    return (
        <div id="upvotes-section">
            <button onClick={() => upvoteArticle()}>upvote</button>
            <p>This article is upvoted {upvotes} times</p>
        </div>
    );
}

export default UpvoteSection;