import React, { useState } from 'react';

const AddComment = ({ articleName, comments, setArticleInfo }) => {
    const [name, setName] = useState("");
    const [comment, setComment] = useState("");

    const commentArticle = async () => {
        const result = await fetch(`/api/articles/${articleName}/add-comment`, {
            method: 'post',
            body: JSON.stringify({ username: name, text: comment }),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body = await result.json();
        console.log(body);
        setArticleInfo(body);
        setComment("");
        setName("");
    }

    return (
        <div id="add-comment-form">
            <label>
                Name: <input type="text" value={name} onChange={e => setName(e.target.value)} />
            </label>
            <label>
                Comment: <textarea value={comment} rows="4" cols="50" onChange={e => setComment(e.target.value)} />
            </label>

            <button onClick={() => commentArticle()}>Comment</button>
        </div>
    );
}

export default AddComment;